.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  background-color: white;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.container {
  padding: 2px 16px;
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.pLbl {
  width: 70px
}

.slider-container {
  display: flex;
  align-items: center;
  width: 200px; /* Adjust width as needed */
}

.slider-container input[type="range"] {
  -webkit-appearance: none; /* Remove default appearance */
  width: 100%;
  height: 10px;
  background: #ddd;
  border-radius: 5px;
  outline: none; /* Remove default focus outline */
  cursor: pointer;
}

.slider-container input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none; /* Remove default thumb appearance */
  width: 20px;
  height: 20px;
  background: #4CAF50; /* Change thumb color */
  border-radius: 50%;
  cursor: pointer;
}

.slider-value {
  margin-left: 10px;
}