body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.topnav {
  height: 60px;
  overflow: hidden;
  background-color: #165396;
}

.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 20px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: #ec1d25;
  color: white;
}

.topnav .serialConn {
  background-color: #0e8f2a;
  color: white;
}

.top-logo {
  padding-bottom: 50px;
  padding-left: 6px;
  /* margin-bottom: 5px; */
  height: 25px;
}

.top-lbl {
  padding-top: 5px;
  padding-right: 15%;
  /* position: relative; */
}